import React, { useState, useEffect } from 'react';

const CountdownTimer = () => {
  const [daysLeft, setDaysLeft] = useState(0);

  useEffect(() => {
    const calculateDaysLeft = () => {
      const targetDate = new Date('2023-07-08');
      const today = new Date();
      const differenceInTime = targetDate.getTime() - today.getTime();
      const daysRemaining = Math.ceil(differenceInTime / (1000 * 3600 * 24));

      setDaysLeft(daysRemaining);
    };

    calculateDaysLeft();

    const timer = setInterval(calculateDaysLeft, 1000);

    return () => clearInterval(timer);
  }, []);

  return (
    <div>
      
      <p className="mt-10 mb-7  font-bold text-2xl">Faltan <span className='text-yellow font-bold'>{daysLeft} días</span> para la fiesta.</p>
    </div>
  );
};

export default CountdownTimer;
