import React, { useState } from 'react';

const WhatsAppForm = () => {
  const [name, setName] = useState('');
  const [message, setMessage] = useState('');

  const handleNameChange = (e) => {
    setName(e.target.value);
  };

  const handleMessageChange = (e) => {
    setMessage(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const whatsappUrl = `https://wa.me/543855914182?text=Hola%20Ricardo,%20mi%20nombre%20es%20${encodeURIComponent(
      name
    )}.%20${encodeURIComponent(message)}`;

    window.open(whatsappUrl, '_blank');
  };

  return (
    <form onSubmit={handleSubmit}>
      <div className="md:flex md:justify-between gap-16 mt-5" >
        <label htmlFor="name" className='font-white font-opensans '></label>
        <input className="w-full bg-blue font-semibold placeholder-opaque-black p-3" type="text" id="name" placeholder='TU NOMBRE' value={name} onChange={handleNameChange} />
      </div>
      <div className="md:flex md:justify-between gap-16 mt-5"  >
        <label htmlFor="message"></label>
        <textarea className="w-full bg-blue font-semibold placeholder-opaque-black p-3" id="message" placeholder='TU CONFIRMACIÓN (si voy/no voy)' value={message} onChange={handleMessageChange} rows="4"
            cols="50" ></textarea>
      </div>
      <button className="p-5 bg-yellow font-semibold text-deep-blue mt-5 hover:bg-red hover:text-white transition duration-500" type="submit">Enviar mensaje por WhatsApp</button>
    </form>
  );
};

export default WhatsAppForm;
